.header {
  position: fixed;
  width: 100%;
  height: 5rem;
  background: linear-gradient(190deg, rgb(15, 2, 86) 0%, rgb(38, 9, 155) 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 3%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.header > button {
  font: inherit;
  cursor: pointer;
  background-color: #1901b6;
  border: 1px solid #1901b6;
  color: white;
  padding: 0.35rem 2rem;
  border-radius: 20px;
  font-weight: bold;
}

.header > button:hover,
.header > button:active {
  background-color: #1f144e;
  border-color: #1f144e;
}

.main-image {
  width: 100%;
  height: 30rem;
  z-index: 0;
  overflow: hidden;
}

.main-image img {
  width: 110%;
  height: 80%;
  object-fit: cover;
  transform: rotateZ(7deg) translateY(-2rem) translateX(-1rem);
}
